[data-accordion-header] {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  cursor: pointer;
  font-size: 1.3rem;
  // font-family: serif;
  padding: 0;
  text-align: start;

  font-weight: 300;

}

.accordionIcon {
  position: relative;
  width: 30px;
  height: 2px;

  height: 1px;
  background-color: currentColor;
  cursor: pointer
}

.line {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 2px;

  height: 1px;
  background-color: inherit;
  transition: transform .2s
}

.line::after {
  content: "";
  position: absolute
}

[aria-expanded=true] .line {
  transform: translate(0, -50%)
}

[aria-expanded=false] .line {
